/* Estilos gerais */
body {
  font-family: 'Arial', sans-serif;
  margin: 0;
  padding: 0;
  background-color: #f4f7f6;
}

/* Contêiner principal */
.employee-container {
  width: 80%;
  max-width: 800px;
  margin: 20px auto;
  padding: 20px;
  background-color: white;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

/* Título */
h1 {
  text-align: center;
  color: #333;
}

/* Estilos do formulário */
form {
  display: flex;
  flex-direction: column;
  gap: 15px;
}

/* Inputs do formulário */
input[type="text"],
input[type="number"],
input[type="date"],
button[type="submit"] {
  padding: 10px;
  font-size: 1rem;
  border: 1px solid #ccc;
  border-radius: 5px;
  background-color: #f9f9f9;
  transition: all 0.3s ease;
}

input[type="text"]:focus,
input[type="number"]:focus,
input[type="date"]:focus {
  border-color: #007bff;
  outline: none;
  background-color: #e9f7ff;
}

button[type="submit"] {
  background-color: #28a745;
  color: white;
  font-weight: bold;
  cursor: pointer;
}

button[type="submit"]:hover {
  background-color: #218838;
}

/* Botões extras */
button {
  font-size: 1rem;
  border: none;
  border-radius: 5px;
  padding: 10px;
  cursor: pointer;
}

button:hover {
  opacity: 0.8;
}

button[type="button"] {
  background-color: #007bff;
  color: white;
}

button[type="button"]:hover {
  background-color: #0056b3;
}

/* Estilo da lista de funcionários */
.employee-list {
  list-style-type: none;
  padding: 0;
}

.employee-item {
  display: flex;
  justify-content: space-between;
  padding: 15px;
  margin-bottom: 15px;
  border: 1px solid #ddd;
  border-radius: 8px;
  background-color: #fafafa;
  transition: background-color 0.3s ease;
}

.employee-item:hover {
  background-color: #f1f1f1;
}

.employee-details {
  max-width: 75%;
}

.remove-btn {
  background-color: #dc3545;
  color: white;
  padding: 10px 15px;
  font-weight: bold;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

.remove-btn:hover {
  background-color: #c82333;
}

/* Estilo da seção de aceitação de termos */
.privacy-policy {
  display: flex;
  align-items: center;
  gap: 10px;
}

.privacy-policy label {
  font-size: 0.9rem;
}

.privacy-policy button {
  font-size: 0.9rem;
  background-color: transparent;
  color: #007bff;
  border: none;
  cursor: pointer;
}

.privacy-policy button:hover {
  text-decoration: underline;
}

/* Estilo do botão de exportação */
.export-btn {
  background-color: #ffc107;
  color: black;
  font-weight: bold;
  padding: 10px 15px;
  border-radius: 5px;
  cursor: pointer;
  display: block;
  margin: 20px auto;
  width: 200px;
  text-align: center;
}

.export-btn:hover {
  background-color: #e0a800;
}

/* Responsividade */
@media (max-width: 600px) {
  .employee-container {
    width: 95%;
    padding: 15px;
  }

  form {
    gap: 10px;
  }

  .employee-item {
    flex-direction: column;
    gap: 10px;
  }

  .employee-details {
    max-width: 100%;
  }
}
